<template>
  <div class="rescue">
    <ul>
      <li
        @click="go(item.uuid, item.point_status)"
        v-for="(item, index) in data"
        :key="index"
      >
        <div class="div_left">
          <img :src="item.img_url" alt />
        </div>
        <div class="div_right">
          <div class="r_title font">{{ item.theme }}</div>
          <div class="r_note font">共{{ item.batch_num }}批</div>
          <div class="r_count">
            <span
              class="count_praise"
              @click.stop="point(item.uuid, item.point_status)"
            >
              <i
                class="iconfont iconxin"
                v-show="item.point_status == true"
              ></i>
              <i
                class="iconfont iconxin-"
                v-show="item.point_status == false"
              ></i>
              {{ item.point_num | toFixed }}
            </span>
            <span class="count_words">{{ item.mw_num | toFixeds }}条留言</span>
          </div>
        </div>
      </li>
      <div class="loading" v-show="state"></div>
    </ul>
  </div>
</template>
<script>
import { mapGetters } from "vuex";
import { getList, updatePoint } from "@/api/team_api";
export default {
  data() {
    return {
      data: [],
      state: true,
      city: "上海",
    };
  },
  filters: {
    toFixed(num) {
      if (num >= 10000) {
        return (num / 10000).toFixed(1) + "万";
      }
      return num + "次";
    },
    toFixeds(num) {
      if (num >= 10000) {
        return (num / 10000).toFixed(1) + "万";
      }
      return num;
    }
  },
  computed: {
    ...mapGetters(["token"])
  },
  created() {},
  mounted() {
    let _this = this;
    this.$nextTick(() => {
      let id = setInterval(() => {
          // console.log(1)
        if (!_this.token) {
        } else {
          clearInterval(id);
          _this.getData();
        }
      }, 500);
    });
  },
  methods: {
    point(uuid, state) {
      // console.log(uuid)
      let _this = this;
      if (!state) {
        updatePoint(uuid).then(data => {
          // console.log('成功')
          _this.getData();
          _this.$toast("点赞成功", { duration: 1500 });
        });
      } else {
        _this.$toast("今日已点赞", { duration: 1500 });
      }
    },
    getData() {
      let _this = this;
      getList(this.city).then(res => {
        _this.data = res.data;
        _this.state = false;
      });
    },
    go(id, status) {
      this.$router.push({
        path: "/MessageBoard",
        query: { id, isShow: 0, status }
      });
    }
  }
};
</script>
<style lang="scss" scoped>
.rescue {
  width: 100%;
  min-height: 150px;
  background-color: #f8f8f8;
  ul {
    width: 100%;
    height: auto;
    padding: 10px 0;
    .loading {
      margin: 1em auto 0;
      width: 40px;
      height: 40px;
      border-radius: 100%;
      border: 2px #f8f8f8 solid;
      border-right-color: #87ceeb;
      animation: loading 1s linear infinite;
    }

    @keyframes loading {
      0% {
        transform: rotate(0deg);
      }
      100% {
        transform: rotate(360deg);
      }
    }
    li {
      margin: 0 15px 15px 15px;
      height: 120px;
      background-color: #fff;
      border-radius: 6px;
      overflow: hidden;

      > div {
        height: 100%;
        float: left;
        padding: 10px;
      }
      .div_left {
        width: 50%;
        // background: url("../../../static/images/item1.png") no-repeat;
        // background-size: 100% 100%;
        img {
          width: 100%;
          height: 100%;
        }
      }
      .div_right {
        width: 50%;
        .font {
          margin-bottom: 15px;
          font-weight: normal;
          font-stretch: normal;
        }
        .r_title {
          letter-spacing: 0.4px;
          color: #333333;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
        }
        .r_note {
          letter-spacing: 0px;
          color: #666666;
        }
        .r_count {
          display: flex;
          justify-content: space-between;
        }
        .count_praise {
          padding: 4px 5px;
          background-color: #ffb4b4;
          border-radius: 20px;
          font-size: 12px;
          color: #ffffff;
          i {
            font-size: 12px;
            color: red;
          }
        }
        .count_words {
          padding: 4px 5px;
          background-color: #f4b79d;
          border-radius: 20px;
          font-size: 12px;
          color: #ffffff;
        }
      }
    }
  }
}
</style>



